@import url(https://fonts.googleapis.com/css2?family=Vollkorn:wght@400;500;600;700;800;900&display=swap);
*{
  margin: 0;
  padding: 0;
  font-family: 'Vollkorn', serif, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}


@media (min-width: 375px){

.navbar{
    margin: 2.5vh 15vw;
}

.inner_navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.inner_navbar h1{
    font-size: 4.9vh;
}

.item_div{
    display: flex;
    align-items: center;
}

#nav-toggle{
    display: none;
}

.item_list{
    margin-top: 0.7541478129713424vh;
    margin-left: 3.0165912518853695vh;
}

li{
    cursor: pointer;
    list-style-type: none;
    display: inline-block;
    margin-left: 3.770739064856712vh;
}

li:hover{
    color: hsl(26, 100%, 55%);
}

.icon_div{
    align-items: center;
    display: flex;
}

.icon_div div{
    margin-top: 1.0558069381598794vh;
    display: flex;
    margin-right: 3.0165912518853695vh;
}

.icon_div div:active {
	position:relative;
	top:1px;
}

.icon_div button{
    position: relative;
    right: 1.5082956259426847vh;
    bottom: 0.7541478129713424vh;
    border: none;
    border-radius: 10px;
    box-shadow: 0px 10px 14px -7px hsl(26, 100%, 55%);
	background-color:hsl(26, 100%, 55%);
    color: white;
    width: 2.7149321266968327vh;
    height: 1.9607843137254901vh;
    font-size: 1.5082956259426847vh;
    cursor: pointer;
}

.icon_cart{
    cursor: pointer;
}

.icon_profile{
    cursor: pointer;
    height: 6.787330316742081vh;
}

.icon_profile:hover{
    border: 2px solid hsl(26, 100%, 55%);
    border-radius: 4.524886877828054vh;
}

.icon_profile:active {
	position:relative;
	top:1px;
}

.hr_line{
    margin-top: 3.770739064856712vh;
    border: 0;
    height: 0.15082956259426847vh;
    border-radius: 100px;
    background: #000;
    opacity: 0.1;
}

.cart_div{
    height: 30.165912518853695vh;
    width: 42.23227752639517vh;
    float: right;
    margin-top: 2vh;
    margin-right: 2.8099547511312217vh;
    border: none;
    border-radius: 7px;
    box-shadow: 5px 5px 20px -7px hsl(0, 0%, 71%);
	background-color:rgb(255, 255, 255);
    color: rgb(190, 190, 190);
}

.cart_div > p{
    display: flex;
    height: 6.033182503770739vh;
    align-items: center;
    margin-left: 3.0165912518853695vh;
    font-weight: bold;
    color: black;
    font-size: 2.262443438914027vh;
}

.cart_div hr{
    height: 0.15082956259426847vh;
    border-radius: 100px;
    background: #000;
    opacity: 0.2;
}

.cart_content{
    height: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 2.262443438914027vh;
}

.cart_content > p{
    font-weight: bold;
    color: rgb(94, 94, 94);
    font-size: 2.262443438914027vh;
    /* display: none; */
}

.checkout_div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.checkout_div > div{
    display: flex;
    align-items: center;
}

.checkout_thumb_image{
    height: 6.033182503770739vh;
    border-radius: 7px;
}

.checkout_div > div > div{
    margin: 0px 1.5082956259426847vh;
}

.checkout_div > div > div > div{
    display: flex;
    margin-top: 0.45248868778280543vh;
}

.checkout_div p{
    font-size: 1.9607843137254901vh;
    color: rgb(148, 148, 148);
}

.checkout_div > div > div > div p:last-child{
    color: rgb(0, 0, 0);
    font-weight: bold;
    margin-left: 1.0558069381598794vh;
}

.checkout_delete_image{
    height: 2.262443438914027vh;
    cursor: pointer;
}

.checkout_delete_image:active {
	position:relative;
	top:1px;
}

.checkout_div button{
    height: 6.033182503770739vh;
    width: 100%;
    border: none;
    border-radius: 7px;
    box-shadow: 0px 10px 14px -7px hsl(26, 100%, 55%);
	background-color:hsl(26, 100%, 55%);
	cursor:pointer;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    margin-top: 2.262443438914027vh;
}

.checkout_div button:active {
	position:relative;
	top:1px;
}

}

/* Responsive  */

@media (max-width: 375px){

    .inner_navbar{
        margin: 0px;
        padding: 5px 15px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: fixed;
        z-index: 10;
        left: 0;
        right: 0;
        top: 0;
        height: 50px;
        background: white;
    }

    .inner_navbar h1{
        font-size: 23px;
        margin-top: 7px;
    }

    #nav-toggle{
        position: absolute;
        top: -100px;
    }

    .icon-burger .line{
        width: 20px;
        height: 3px;
        background-color: rgb(0, 0, 0);
        margin: 3px 10px 0px 0px;
        border-radius: 20px;
        transition: all .3s ease-in-out;
    }
    
    .item_div{
        display: flex;
        align-items: center;
    }
    
    .item_list{
        margin-top: 0px;
        margin-left: 0px;
    }

    ul{
        float: none;
        position: fixed;
        z-index: 9;
        left: 0;
        right: 0;
        top: 50px;
        bottom: 100%;
        width: auto;
        height: auto;
        flex-direction: column;
        justify-content: space-between;
        background-color: rgba(0, 0, 0, .8);
        overflow: hidden;
        transition: all .5s ease-in-out;
    }

    ul li{
        display: block;
        text-align: center;
        color: white;
        font-size: 20px;
        margin-top: 30px;
    }

    :checked ~ ul{
        bottom: 0;
    }

    :checked ~ .icon-burger .line:nth-child(1){
        -webkit-transform: translateY(6px) rotate(45deg);
                transform: translateY(6px) rotate(45deg);
    }

    :checked ~ .icon-burger .line:nth-child(2){
        opacity: 0;
    }

    :checked ~ .icon-burger .line:nth-child(3){
        -webkit-transform: translateY(-6px) rotate(-45deg);
                transform: translateY(-6px) rotate(-45deg);
    }

    .icon_div{
        align-items: center;
        display: flex;
    }
    
    .icon_div div{
        margin-top: 2px;
        display: flex;
        margin-right: 10px;
    }
    
    .icon_div div:active {
        position:relative;
        top:1px;
    }
    
    .icon_div button{
        position: relative;
        right: 6px;
        bottom: 3px;
        border: none;
        border-radius: 10px;
        box-shadow: 0px 10px 14px -7px hsl(26, 100%, 55%);
        background-color:hsl(26, 100%, 55%);
        color: white;
        width: 17px;
        height: 12px;
        font-size: 8px;
        cursor: pointer;
    }
    
    .icon_profile{
        cursor: pointer;
        height: 30px;
    }
    
    .icon_profile:hover{
        border: 2px solid hsl(26, 100%, 55%);
        border-radius: 4.524886877828054vh;
    }
    
    .icon_profile:active {
        position:relative;
        top:1px;
    }
    
    .hr_line{
        margin: 0px;
        border: 0;
        height: 1px;
        border-radius: 100px;
        background: #000;
        opacity: 0.1;
    }
    
    .cart_div{
        margin: 5px 10px;
        height: 200px;
        float: left;
        border: none;
        border-radius: 7px;
        box-shadow: 5px 5px 20px -7px hsl(0, 0%, 71%);
        background-color:rgb(255, 255, 255);
        color: rgb(190, 190, 190);
        float: none;
        position: fixed;
        z-index: 8;
        left: 0;
        right: 0;
        top: 50px;
        bottom: 0;
        width: auto;
    }
    
    .cart_div > p{
        display: flex;
        height: 40px;
        align-items: center;
        margin-left: 20px;
        font-weight: bold;
        color: black;
        font-size: 15px;
    }
    
    .cart_div hr{
        height: 1px;
        border-radius: 100px;
        background: #000;
        opacity: 0.2;
    }
    
    .cart_content{
        height: 75%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .cart_content > p{
        font-weight: bold;
        color: rgb(94, 94, 94);
        font-size: 15px;
        /* display: none; */
    }
    
    .checkout_div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 90%;
    }
    
    .checkout_div > div{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    
    .checkout_thumb_image{
        height: 50px;
        border-radius: 7px;
    }
    
    .checkout_div > div > div > div{
        display: flex;
        margin-top: 3px;
    }
    
    .checkout_div p{
        font-size: 16px;
        color: rgb(148, 148, 148);
    }
    
    .checkout_div > div > div > div p:last-child{
        color: rgb(0, 0, 0);
        font-weight: bold;
        margin-left: 7px;
    }
    
    .checkout_delete_image{
        height: 17px;
        cursor: pointer;
    }
    
    .checkout_delete_image:active {
        position:relative;
        top:1px;
    }
    
    .checkout_div button{
        height: 40px;
        width: 100%;
        border: none;
        border-radius: 7px;
        box-shadow: 0px 10px 14px -7px hsl(26, 100%, 55%);
        background-color:hsl(26, 100%, 55%);
        cursor:pointer;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 400;
        margin-top: 15px;
    }
    
    .checkout_div button:active {
        position:relative;
        top:1px;
    }   
}
@media (min-width: 375px){
.container{
    margin: 0px 15vw;
    display: flex;
    height: 80vh;
    position: absolute;
}

.change_image{
    display: none;
}

.left_div{
    width: 50%;
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
}

.large_image{
    border-radius: 10px;
    height: 55vh;
}

.small_image_div{
    width: 55vh;
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}

.thumb_image{
    cursor: pointer;
    height: 10vh;
    border-radius: 10px;
}

.thumb_image:hover{
    opacity: 0.8;
}

.thumb_image:active {
	position:relative;
	top:1px;
}

.right_div{
    width: 50%;
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}

.inner_right_div{
    width: 90%;
}

.right_div h6{
  color: hsl(26, 100%, 55%); 
  margin-bottom: 15px;
  font-size: 2.5vh;
}

.right_heading{
    margin-bottom: 25px;
    font-size: 5.5vh;
}

.right_content{
    margin-bottom: 20px;
    font-size: 2.4vh;
}

.price_div{
    display: flex;
    margin-bottom: 2px;
    align-items: center;
}

.price_div h2{
    font-weight: bold;
    font-size: 3.4vh;
}

.price_div button{
    margin-left: 1vh;
    width: 6vh;
    height: 3vh;
    border-radius: 5px;
    background-color: hsl(25, 100%, 94%);
    border: none;
    color: hsl(26, 100%, 55%);
    font-weight: bold;
    padding: 2px;
    font-size: 2vh;
}

.real_price{
    margin-bottom: 20px;
    font-size: 2.4vh;
    opacity: 0.3;
    font-weight: bold;
    text-decoration: line-through;
}

.button_div{
    display: flex;
    align-items: center;
}

.button_inner_div{
    display: flex;
}

.minusbutton{
    cursor: pointer;
    height: 6vh;
    width: 5.2vh;
    border: none;
    border-radius: 7px 0px 0px 7px;
    color: hsl(26, 100%, 55%);
    font-weight: bold;
    font-size: 3.770739064856712vh;
    background-color: rgba(224, 224, 224, 0.3);
}

.countbutton{
    height: 6vh;
    width: 7.5vh;
    border: none;
    color: hsl(0, 0%, 0%);
    font-weight: bold;
    font-size: 2.7149321266968327vh;
    background-color: rgba(224, 224, 224, 0.3);
}

.plusbutton{
    cursor: pointer;
    height: 6vh;
    width: 5.2vh;
    border: none;
    border-radius: 0px 7px 7px 0px;
    color: hsl(26, 100%, 55%);
    font-weight: bold;
    font-size: 3.770739064856712vh;
    background-color: rgba(224, 224, 224, 0.3);
}

.addtocart_button{
    margin-left: 3vh;
    height: 6vh;
    width: 30.1vh;
    border: none;
    border-radius: 7px;
    box-shadow: 0px 10px 14px -7px hsl(26, 100%, 55%);
	background-color:hsl(26, 100%, 55%);
	cursor:pointer;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2vh;
}

.addtocart_button:active {
	position:relative;
	top:1px;
}

.addtocart_image{
    height: 15px;
}

.addtocart_text{
    margin-left: 10px;
    font-weight: bold;
}

}

